import Vue from "vue";
import Router from "vue-router";

import Home from "@/components/Home.vue";
import Kanmuri from "@/components/Kanmuri.vue";
Vue.use(Router);

export default new Router({
  mode: "history",
  routes: [
    {
      path: "/",
      component: Home
    },
    {
      path: "/kanmuri",
      component: Kanmuri
    }
  ]
});



