<template>
  <div class="home">
    <ol>
      <li v-for="pokemon in pokemons" :key="pokemon.name">
        <div>{{pokemon.no}}</div>
        <div>{{pokemon.name}}</div>
      </li>
    </ol>
  </div>
</template>

<script>
import users from '../assets/garal_data.json'
export default {
  data () {
    return {
      pokemons: users
    }
  }
}
</script>


